	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Any Questions?","date":"29 Sep 2004","slug":"/2004-09-29-any-questions/","tags":"Y2004"},"html":"<p>I haven't posted about my job in a while. Let's see, my laid-off colleagues are now gone, those of us who remain are being absorbed into a new department, our responsibilities are being cut down to a bare minimum, the majority of the employees agreed to a voluntary pay cut (I didn't), the business manager has been kicked upstairs, the chairman has announced his resignation, the September paychecks are late, and we have no word on what will happen next. </p>\n<p>That's enough about work.</p>"},"next":{"fields":{"title":"Showing Up","date":"28 Sep 2004","slug":"/2004-09-28-showing-up/"}},"previous":{"fields":{"title":"Seeing Stars","date":"29 Sep 2004","slug":"/2004-09-29-seeing-stars/"}}},{"node":{"fields":{"title":"Showing Up","date":"28 Sep 2004","slug":"/2004-09-28-showing-up/","tags":"Y2004"},"html":"<p><a href=\"http://bgbg.blogspot.com/2004/09/showing-up-half-battle.html\" title=\"Bag and Baggage - Denise Howell, appellate and intellectual property lawyer\">Bag and Baggage:</a> \"I honestly can't imagine what would prevent a lawyer from showing up for his or her oral argument before the California Supreme Court. If you're going to do it though, try picking <em>anything</em> but a legal malpractice case...\"</p>"},"next":{"fields":{"title":"Vowe's Law","date":"27 Sep 2004","slug":"/2004-09-27-vowes-law/"}},"previous":{"fields":{"title":"Any Questions?","date":"29 Sep 2004","slug":"/2004-09-29-any-questions/"}}},{"node":{"fields":{"title":"Vowe's Law","date":"27 Sep 2004","slug":"/2004-09-27-vowes-law/","tags":"Y2004"},"html":"<p><a href=\"http://vowe.net/archives/005003.html\" title=\"vowe dot net :: What is new in TomTom Navigator 4.12 for palmOne?\">vowe</a>: \"Bluetooth may look interesting but a plain old cable is always better. And you have to hook up everything anyway since all of these devices need power. How often are they fully charged and will operate longer than you need them? Not very likely, according to Murphy's Law.\"</p>"},"next":{"fields":{"title":"Tuna Casserole","date":"27 Sep 2004","slug":"/2004-09-27-the-jean-paul-sartre-cookbook/"}},"previous":{"fields":{"title":"Showing Up","date":"28 Sep 2004","slug":"/2004-09-28-showing-up/"}}}],"pathPrefix":"","first":false,"last":false,"index":413,"pageCount":1101}}

